<template>
 <v-row>
   <v-btn
     class="mt-1 mr-2"
     width="350"
     @click.stop="dialog = true"
   >
   Edit Time
   </v-btn>
   <v-dialog v-model="dialog" scrollable persistent max-width="350">
    <v-card outlined width="350" height="500">
     <v-container fluid>
       <v-row class="ma-0">
        <v-col xs="12">
         <v-card-title>Edit Meetup Time</v-card-title>
        </v-col>
       </v-row>
       <v-divider></v-divider>
       <v-row class="ma-0">
        <v-col xs="12">
          <v-time-picker v-model="editableTime" style="width: 100%" actions format="24hr">
            <template>
              <v-btn class="blue--text darken-1" text @click.native="onSaveChanges">Save</v-btn>
              <v-btn class="blue--text darken-1" text @click.native="dialog = false">Close</v-btn>
            </template>
          </v-time-picker>
        </v-col>
       </v-row>
      </v-container>
    </v-card>
   </v-dialog>
 </v-row>
</template>

<script>
export default {
  props: ['meetup'],
  data () {
    return {
      dialog: false,
      editableTime: ''
    }
  },
  methods: {
    onSaveChanges () {
      const newDate = new Date(this.meetup.date)
      const hours = this.editableTime.match(/^(\d+)/)[1]
      const minutes = this.editableTime.match(/:(\d+)/)[1]
      newDate.setHours(hours)
      newDate.setMinutes(minutes)
      this.$store.dispatch('updateMeetupData', {
        id: this.meetup.id,
        date: newDate
      })
    }
  },
  created () {
    this.editableDate = new Date(this.meetup.date).toTimeString()
  }
}
</script>

