<template>
    <v-container>
        <v-row>
            <v-col xs="12" sm="6" offset-sm="3">
                <h2>Create a new meetup</h2>
            </v-col>
        </v-row>
        <v-row>
            <v-col>
                <form @submit.prevent="onCreateMeetup">
                    <v-row>
                        <v-col xs="12" sm="6" offset-sm="3">
                            <v-text-field
                             name="title"
                             label="Title"
                             id="title"
                             v-model="title"
                             required
                            ></v-text-field>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col xs="12" sm="6" offset-sm="3">
                            <v-text-field
                             name="location"
                             label="Location"
                             id="location"
                             v-model="location"
                             required
                            ></v-text-field>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col xs="12" sm="6" offset-sm="3">
                            <v-btn raised class="primary" @click="onPickFile">Upload Image</v-btn>
                            <input
                            type="file"
                            style="display: none;"
                            ref="fileInput"
                            accept="image/*"
                            @change="onFilePicked">
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col xs="12" sm="6" offset-sm="3">
                            <img height="150" :src="imageUrl" >
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col xs="12" sm="6" offset-sm="3">
                            <v-textarea
                             name="description"
                             label="Description"
                             id="description"
                             v-model="description"
                             multi-line
                             required
                            ></v-textarea>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col xs="12" sm="6" offset-sm="3">
                            <h4>Choose a Date & Time</h4>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col xs="12" sm="6" offset-sm="3" class="mb-2">
                            <v-date-picker v-model="date"></v-date-picker>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col xs="12" sm="6" offset-sm="3">
                            <v-time-picker v-model="time" format="24hr"></v-time-picker>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col xs="12" sm="6" offset-sm="3">
                            <v-btn
                             class="primary"
                             :disabled="!formIsValid"
                             type="submit"
                            >Create Meetup</v-btn>
                        </v-col>
                    </v-row>
                </form>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
export default {
  data: () => ({
    title: '',
    location: '',
    imageUrl: '',
    description: '',
    date: new Date().toISOString().substr(0, 10),
    time: new Date(),
    image: null
  }),
  computed: {
    formIsValid () {
      return this.title !== '' &&
      this.location !== '' &&
      this.imageUrl !== '' &&
      this.description !== ''
    },
    submitDateTime () {
      const date = new Date(this.date)
      if (typeof this.time === 'string') {
        const hours = this.time.match(/^(\d+)/)[1]
        const minutes = this.time.match(/:(\d+)/)[1]
        date.setHours(hours)
        date.setMinutes(minutes)
      } else {
        date.setHours(this.time.getHours())
        date.setMinutes(this.time.getMinutes())
      }
      return date
    }
  },
  methods: {
    onCreateMeetup () {
      if (!this.formIsValid) {
        return
      }
      if (!this.image) {
        return
      }
      const meetupData = {
        title: this.title,
        location: this.location,
        image: this.image,
        description: this.description,
        date: this.submitDateTime
      }
      this.$store.dispatch('createMeetup', meetupData)
      this.$router.push('/meetups')
    },
    onPickFile () {
      this.$refs.fileInput.click()
    },
    onFilePicked (event) {
      const files = event.target.files
      const filename = files[0].name
      if (filename.lastIndexOf('.') <= 0) {
        return alert('Please add a valid file!')
      }
      const fileReader = new FileReader()
      fileReader.addEventListener('load', () => {
        this.imageUrl = fileReader.result
      })
      fileReader.readAsDataURL(files[0])
      this.image = files[0]
    }
  }
}
</script>

