<template>
 <v-row>
   <v-btn
     class="mt-1 mr-2"
     height="45"
     width="45"
     fab
     @click.stop="dialog = true"
   >
    <v-icon>fas fa-edit</v-icon>
   </v-btn>
   <v-dialog v-model="dialog" scrollable persistent max-width="350">
    <v-card outlined width="350" height="500">
     <v-container fluid>
       <v-row class="ma-0">
        <v-col xs="12">
         <v-card-title>Edit Meetup</v-card-title>
        </v-col>
       </v-row>
       <v-divider></v-divider>
       <v-row class="ma-0">
        <v-col xs="12">
         <v-card-text>
          <v-text-field
           name="title"
           label="Title"
           id="title"
           v-model="editedTitle"
           required
          ></v-text-field>
          <v-textarea
           name="description"
           label="Description"
           id="description"
           v-model="editedDescription"
           multi-line
           required
          ></v-textarea>
         </v-card-text>
        </v-col>
       </v-row>
       <v-divider></v-divider>
       <v-row>
        <v-col xs="12">
         <v-card-actions>
           <v-btn text class="blue--text darken-1" @click="onSaveChanges">Save</v-btn>
           <v-btn
           text
           class="blue--text darken-1"
           @click="dialog = false"
           >Cancel</v-btn>
         </v-card-actions>
        </v-col>
       </v-row>
      </v-container>
    </v-card>
   </v-dialog>
 </v-row>
</template>

<script>
export default {
  props: ['meetup'],
  data () {
    return {
      editedTitle: this.meetup.title,
      editedDescription: this.meetup.description,
      dialog: false
    }
  },
  methods: {
    onSaveChanges () {
      if (this.editedTitle.trim() === '' || this.editedDescription.trim() === '') {
        return
      }
      this.dialog = false
      this.$store.dispatch('updateMeetupData', {
        id: this.meetup.id,
        title: this.editedTitle,
        description: this.editedDescription
      })
    }
  }
}
</script>

