<template>
    <v-container>
        <v-row class="mb-2">
            <v-col xs="12" sm="6" class="text-center text-md-right">
                <v-btn large router to="/meetups" class="info">Explore Meetups</v-btn>
            </v-col>
            <v-col xs="12" sm="6" class="text-center text-md-left">
                <v-btn large router to="/meetup/new" class="info">Organize Meetups</v-btn>
            </v-col>
        </v-row>
        <v-row>
            <v-col xs="12" class="text-center">
                <v-progress-circular
                indeterminate
                color="primary"
                :width="7"
                :size="70"
                v-if="loading"
                ></v-progress-circular>
            </v-col>
        </v-row>
        <v-row v-if="!loading">
            <v-col xs="12">
                <v-carousel class="cursor: pointer;">
                    <v-carousel-item
                    v-for="(meetup,i) in meetups"
                    :key="i"
                    :src="meetup.imageUrl"
                    @click="onLoadMeetup(meetup.id)"
                    reverse-transition="fade-transition"
                    transition="fade-transition"
                    >
                    <div class="text-center title">{{ meetup.title }}</div>
                    </v-carousel-item>
                </v-carousel>
            </v-col>
        </v-row>
        <v-row class="mt-2">
            <v-col xs="12" class="text-center">
                <p>Web for Creating and Registering for meetups</p>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
export default {
  computed: {
    meetups () {
      return this.$store.getters.featuredMeetups
    },
    loading () {
      return this.$store.getters.loading
    }
  },
  methods: {
    onLoadMeetup (id) {
      this.$router.push(`/meetups/${id}`)
    }
  }
}
</script>

