<template>
 <v-row>
   <v-btn
     class="mt-1 mr-2"
     width="350"
     @click.stop="dialog = true"
   >
   Edit Date
   </v-btn>
   <v-dialog v-model="dialog" scrollable persistent max-width="350">
    <v-card outlined width="350" height="500">
     <v-container fluid>
       <v-row class="ma-0">
        <v-col xs="12">
         <v-card-title>Edit Meetup Date</v-card-title>
        </v-col>
       </v-row>
       <v-divider></v-divider>
       <v-row class="ma-0">
        <v-col xs="12">
          <v-date-picker v-model="editableDate" style="width: 100%" actions>
            <template>
              <v-btn class="blue--text darken-1" text @click.native="onSaveChanges">Save</v-btn>
              <v-btn class="blue--text darken-1" text @click.native="dialog = false">Close</v-btn>
            </template>
          </v-date-picker>
        </v-col>
       </v-row>
      </v-container>
    </v-card>
   </v-dialog>
 </v-row>
</template>

<script>
export default {
  props: ['meetup'],
  data () {
    return {
      dialog: false,
      editableDate: ''
    }
  },
  methods: {
    onSaveChanges () {
      const newDate = new Date(this.meetup.date)
      const newDay = new Date(this.editableDate).getUTCDate()
      const newMonth = new Date(this.editableDate).getUTCMonth()
      const newYear = new Date(this.editableDate).getUTCFullYear()
      newDate.setUTCDate(newDay)
      newDate.setUTCMonth(newMonth)
      newDate.setUTCFullYear(newYear)
      this.$store.dispatch('updateMeetupData', {
        id: this.meetup.id,
        date: newDate
      })
    }
  },
  created () {
    this.editableDate = ''
  }
}
</script>

