<template>
    <v-container>
        <v-row v-if="loading">
            <v-col xs="12" class="text-center">
                <v-progress-circular
                indeterminate
                color="primary"
                :width="7"
                :size="70"
                ></v-progress-circular>
            </v-col>
        </v-row>
        <v-row v-else>
            <v-col xs="12">
                <v-card>
                    <v-card-title class="d-inline-flex"><h4 class="primary--text">{{ meetup.title }}</h4></v-card-title>
                    <div v-if="userIsCreator" class="float-right mr-3 mt-1">
                        <app-edit-details :meetup="meetup"></app-edit-details>
                    </div>
                    <v-img height="400px" :src="meetup.imageUrl"></v-img>
                    <v-card-text>
                        <div class="info--text">{{ meetup.date | date }} - {{ meetup.location }}</div>
                        <div>
                          <app-edit-date :meetup="meetup" v-if="userIsCreator"></app-edit-date>
                          <app-edit-time :meetup="meetup" v-if="userIsCreator"></app-edit-time>
                        </div>
                        <div>{{ meetup.description }}</div>
                    </v-card-text>
                    <v-row>
                        <v-col class="text-right">
                            <v-card-actions>
                                <app-register
                                :meetupId="meetup.id"
                                v-if="userIsAuthenticated && !userIsCreator"></app-register>
                            </v-card-actions>
                        </v-col>
                    </v-row>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
export default {
  props: ['id'],
  computed: {
    meetup () {
      return this.$store.getters.loadedMeetup(this.id)
    },
    userIsAuthenticated () {
      return this.$store.getters.user !== null && this.$store.getters.user !== undefined
    },
    userIsCreator () {
      if (!this.userIsAuthenticated) {
        return false
      }
      return this.$store.getters.user.id === this.meetup.creatorId
    },
    loading () {
      return this.$store.getters.loading
    }
  }
}

</script>

