<template>
  <v-app dark>
    <v-navigation-drawer v-model="sideNav" absolute temporary>
      <v-list>
        <v-list-item
          v-for="item in menuItems"
          :key="item.title"
          :to="item.link">
          <v-list-item-action>
            <v-icon>{{ item.icons }}</v-icon>
          </v-list-item-action>
          <v-list-item-content>{{ item.title }}</v-list-item-content>
        </v-list-item>
        <v-list-item v-if="userIsAuthenticated" @click="onLogOut">
          <v-list-item-action>
            <v-icon>fas fa-sign-out-alt</v-icon>
          </v-list-item-action>
          <v-list-item-content>Log Out</v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-card>
      <v-toolbar dark elevation class="blue darken-5">
        <v-icon @click.native.stop="sideNav =! sideNav" left class="d-flex d-sm-flex d-md-none">fas fa-bars</v-icon>
        <v-toolbar-title>
          <router-link to="/" tag="span" style="cursor: pointer">Meetup</router-link>
        </v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-items class="d-none d-md-flex">
          <v-btn
            text
            v-for="item in menuItems"
            :key="item.title"
            :to="item.link">
            <v-icon left>{{ item.icons }}</v-icon>
            {{ item.title }}
          </v-btn>
          <v-btn text v-if="userIsAuthenticated" @click="onLogOut">
            <v-icon left>fas fa-sign-out-alt</v-icon>
            Log Out
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>
    </v-card>
    <main>
      <router-view></router-view>
    </main>
  </v-app>
</template>

<script>

export default {
  name: 'App',
  data: () => ({
    sideNav: false
  }),
  computed: {
    menuItems () {
      let menuItems = [
        { icons: 'fas fa-sign-in-alt', title: 'Sign up', link: '/signup' },
        { icons: 'fas fa-lock-open', title: 'Sign in', link: '/signin' }
      ]
      if (this.userIsAuthenticated) {
        menuItems = [
          { icons: 'fas fa-calendar', title: 'View Meetups', link: '/meetups' },
          { icons: 'fas fa-map-marker-alt', title: 'Organize Meetup', link: '/meetup/new' }
        ]
      }
      return menuItems
    },
    userIsAuthenticated () {
      return this.$store.getters.user !== null && this.$store.getters.user !== undefined
    }
  },
  methods: {
    onLogOut () {
      this.$store.dispatch('Logout')
      this.$router.push('/')
    }
  }
}
</script>
