<template>
 <v-row>
   <v-btn
     class="mt-1 mr-2"
     width="350"
     @click.stop="dialog = true"
   >
   {{ userIsRegistered ? 'Unregister' : 'Register' }}
   </v-btn>
   <v-dialog v-model="dialog" scrollable persistent max-width="350">
    <v-card outlined width="350" height="500">
     <v-container fluid>
       <v-row class="ma-0">
        <v-col xs="12">
         <v-card-title v-if="userIsRegistered">Unregister from meetup?</v-card-title>
         <v-card-title v-else>Register for Meetup</v-card-title>
        </v-col>
       </v-row>
       <v-divider></v-divider>
       <v-row class="ma-0">
        <v-col xs="12">
          <v-card-text>You can always change your decision later on.</v-card-text>
        </v-col>
       </v-row>
       <v-row class="ma-0">
        <v-col xs="12">
          <v-card-actions>
           <v-btn
           class="red--text darken1"
           text
           @click="onAgree"
           >
           Confirm</v-btn>
           <v-btn
           class="green--text darken-1"
           text
           @click="dialog = false"
           >
           Cancel</v-btn>
          </v-card-actions>
        </v-col>
       </v-row>
      </v-container>
    </v-card>
   </v-dialog>
 </v-row>
</template>

<script>
export default {
  data () {
    return {
      dialog: false
    }
  },
  props: ['meetupId'],
  computed: {
    userIsRegistered () {
      return this.$store.getters.user.registeredMeetups.findIndex(meetupId => {
        return meetupId === this.meetupId
      }) >= 0
    }
  },
  methods: {
    onAgree () {
      if (this.userIsRegistered) {
        this.$store.dispatch('unregisterUserForMeetup', this.meetupId)
      } else {
        this.$store.dispatch('registerUserForMeetup', this.meetupId)
      }
    }
  }
}
</script>

