import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import 'roboto-fontface/css/roboto/roboto-fontface.css'
import '@fortawesome/fontawesome-free/css/all.css'
import DateFilter from './filter/date'
import * as firebase from 'firebase/app'
import AlertBox from './components/Shared/Alert.vue'
import EditMeetupDetailsDialog from './components/Meetup/Edit/EditMeetupDetailsDialog.vue'
import EditMeetupDateDialog from './components/Meetup/Edit/EditMeetupDateDialog.vue'
import EditMeetupTimeDialog from './components/Meetup/Edit/EditMeetupTimeDialog.vue'
import RegisterDialog from './components/Meetup/Registration/RegisterDialog.vue'

Vue.config.productionTip = false
Vue.filter('date', DateFilter)
Vue.component('app-alert', AlertBox)
Vue.component('app-edit-details', EditMeetupDetailsDialog)
Vue.component('app-edit-date', EditMeetupDateDialog)
Vue.component('app-edit-time', EditMeetupTimeDialog)
Vue.component('app-register', RegisterDialog)

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App),
  created () {
    firebase.initializeApp({
      apiKey: 'AIzaSyBcy4hO48xhi_QSB6nbL3wRtnjXh-lJ0QA',
      authDomain: 'meetup-3ad67.firebaseapp.com',
      databaseURL: 'https://meetup-3ad67.firebaseio.com',
      projectId: 'meetup-3ad67',
      storageBucket: 'meetup-3ad67.appspot.com',
      messagingSenderId: '1054253687691',
      appId: '1:1054253687691:web:46872b29cfdb743386458e'
    })
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.$store.dispatch('autoSignIn', user)
        this.$store.dispatch('fetchUserData')
        this.$store.dispatch('fetchUserProfile')
      }
    })
    this.$store.dispatch('loadMeetups')
  }
}).$mount('#app')
