import firebase from 'firebase/app'
require('firebase/auth')
require('firebase/database')
require('firebase/storage')

export default {
  state: {
    user: null
  },
  mutations: {
    registerUserForMeetup (state, payload) {
      const id = payload.id
      if (state.user.registeredMeetups.findIndex(meetup => meetup.id === id) >= 0) {
        return
      }
      state.user.registeredMeetups.push(id)
      state.user.fbKey[id] = payload.fbKey
    },
    unregisterUserForMeetup (state, payload) {
      const registeredMeetups = state.user.registeredMeetups
      registeredMeetups.splice(registeredMeetups.findIndex(meetup => meetup.id === payload), 1)
      Reflect.deleteProperty(state.user.fbKey, payload)
    },
    setUser (state, payload) {
      state.user = payload
    }
  },
  actions: {
    registerUserForMeetup ({ commit, getters }, payload) {
      commit('setLoading', true)
      const user = getters.user
      firebase.database().ref(`/users/${user.id}`).child('/registrations/').push(payload)
        .then(data => {
          commit('setLoading', false)
          commit('registerUserForMeetup', { id: payload, fbKey: data.key })
        })
        .catch(error => {
          console.log(error)
          commit('setLoading', false)
        })
    },
    unregisterUserForMeetup ({ commit, getters }, payload) {
      commit('setLoading', true)
      const user = getters.user
      if (!user.fbKey) {
        return
      }
      const fbKey = user.fbKey[payload]
      firebase.database().ref(`/users/${user.id}/registrations`).child(fbKey)
        .remove()
        .then(() => {
          commit('setLoading', false)
          commit('unregisterUserForMeetup', payload)
        })
        .catch(error => {
          console.log(error)
          commit('setLoading', false)
        })
    },
    async signUserUp ({ commit }, payload) {
      commit('setLoading', true)
      commit('clearError')
      await firebase.auth().createUserWithEmailAndPassword(payload.email, payload.password)
        .then(
          data => {
            commit('setLoading', false)
            const newUser = {
              id: data.user.uid,
              registeredMeetups: [],
              fbKey: {}
            }
            commit('setUser', newUser)
          }
        )
        .catch(
          error => {
            commit('setLoading', false)
            commit('setError', error)
          }
        )
    },
    async signUserIn ({ commit }, payload) {
      commit('setLoading', true)
      commit('clearError')
      await firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
        .then(
          data => {
            commit('setLoading', false)
            const newUser = {
              id: data.user.uid,
              registeredMeetups: [],
              fbKey: {}
            }
            commit('setUser', newUser)
          }
        )
        .catch(
          error => {
            commit('setLoading', false)
            commit('setError', error)
          }
        )
    },
    autoSignIn ({ commit }, payload) {
      commit('setUser', {
        id: payload.uid,
        registeredMeetups: [],
        fbKey: {}
      })
    },
    fetchUserProfile ({ commit }) {
      const user = firebase.auth().currentUser
      if (user !== null) {
        console.log(user)
      }
    },
    fetchUserData ({ commit, getters }) {
      commit('setLoading', true)
      firebase.database().ref(`/users/${getters.user.id}/registrations/`).once('value')
        .then(data => {
          const dataPairs = data.val()
          const registeredMeetups = []
          const swappedPairs = {}
          for (const key in dataPairs) {
            registeredMeetups.push(dataPairs[key])
            swappedPairs[dataPairs[key]] = key
          }
          const updatedUser = {
            id: getters.user.id,
            registeredMeetups: registeredMeetups,
            fbKey: swappedPairs
          }
          commit('setLoading', false)
          commit('setUser', updatedUser)
        })
        .catch(error => {
          console.log(error)
          commit('setLoading', false)
        })
    },
    Logout ({ commit }) {
      firebase.auth().signOut()
      commit('setUser', null)
    }
  },
  getters: {
    user (state) {
      return state.user
    }
  }
}
